import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { UseSelector, useDispatch } from "react-redux";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import { addToCart } from "../Actions/cartAction";

import "../css/Pizza.css";
function Pizza({ pizza }) {
  const [quantity, setQuantity] = useState(1);
  const [varient, setVarient] = useState("small");

  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const dispatch = useDispatch();

  function addtocart() {
    dispatch(addToCart(pizza, quantity, varient));
  }

  return (
    <div className="pizza">
      <div id="header" onClick={handleShow}>
        <h1>{pizza.name}</h1>

        <img src={pizza.image} style={{ height: 150, width: 150 }} />
      </div>

      <div id="action-div">
        <div id="varient-div">
          <small style={{ marginLeft: -90 }}>Size </small>
          <select
            value={varient}
            onChange={(e) => {
              setVarient(e.target.value);
            }}
          >
            {pizza.varient.map((varient) => {
              return <option value={varient}>{varient}</option>;
            })}
          </select>
        </div>
        <div id="quantity-div">
          <small style={{ marginLeft: -70 }}>Quantity </small>
          <select
            value={quantity}
            onChange={(e) => {
              setQuantity(e.target.value);
            }}
          >
            {[...Array(10).keys()].map((ele, index) => {
              return <option value={index + 1}>{index + 1}</option>;
            })}
          </select>
        </div>
      </div>
      <div id="action-div2">
        <div>
          <h1>Price : {pizza.prices[0][varient] * quantity} Rs/-</h1>
        </div>
        <div>
          <button id="addButton" onClick={addtocart}>
            Add to cart
          </button>
        </div>
      </div>

      {/* modal */}

      <Modal
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
        style={{
          width: 300,
          marginLeft: 500,
        }}
      >
        <Modal.Header closeButton>
          <Modal.Title style={{ marginLeft: 45 }}>{pizza.name}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <img
            src={pizza.image}
            style={{ width: 200, height: 200, marginLeft: 25 }}
          />
          <p style={{ marginLeft: 10, marginTop: 15 }}>{pizza.description}</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="danger" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}

export default Pizza;
