import React from "react";
import { useSelector, useDispatch } from "react-redux";
import "../css/cart.css";
import { addToCart } from "../Actions/cartAction";
import { deleteFromCart } from "../Actions/cartAction";
import Checkout from "./Checkout";

function Cart() {
  const cartState = useSelector((state) => state.cartReducer);
  const userState = useSelector((state) => state.loginReducer);
  const currentUser = userState.currentUser;
  const cartItems = cartState.cartItems;
  const dispatch = useDispatch();
  const subtotal = cartItems.reduce((x, item) => x + item.price, 0);

  return (
    <div className="cart">
      <div id="cartContainer">
        {cartItems.length < 1 ? (
          <center>
            <h2 id="noItems">
              You have no items in your cart{" "}
              <img
                src="https://static.vecteezy.com/system/resources/previews/013/743/806/original/sad-face-emoji-png.png"
                style={{ width: 30, height: 30 }}
              />
            </h2>
          </center>
        ) : (
          ""
        )}
        {cartItems.map((item) => {
          return (
            <div id="items">
              <div id="itemImage">
                <img src={item.image} style={{ height: 100, width: 100 }} />
              </div>
              <div id="itemName">
                <h4>{item.name}</h4>
                <small>{item.varient}</small>
              </div>
              <div id="itemButton">
                <div id="btn">
                  <i
                    class="fa-regular fa-square-minus"
                    onClick={() => {
                      dispatch(
                        addToCart(item, item.quantity - 1, item.varient)
                      );
                    }}
                  ></i>
                  <span> {item.quantity} </span>
                  <i
                    class="fa-regular fa-square-plus"
                    onClick={() => {
                      dispatch(
                        addToCart(item, item.quantity + 1, item.varient)
                      );
                    }}
                  ></i>
                </div>
              </div>
              <div id="itemPrice">
                <i class="fa-solid fa-indian-rupee-sign"></i>
                {item.quantity * item.prices[0][item.varient]} /-
              </div>
              <div id="trash">
                <img
                  src="https://w1.pngwing.com/pngs/431/160/png-transparent-icon-design-trash-red-line-area-material-rectangle.png"
                  style={{ width: 20, height: 20 }}
                  onClick={() => {
                    dispatch(deleteFromCart(item));
                  }}
                />
                {/* <i class="fa-regular fa-trash-can"></i> */}
              </div>
            </div>
          );
        })}
      </div>

      {cartItems.length > 0 ? (
        <div id="subtotalDiv">
          <div id="subtotalContainer">
            <div id="subtotal">
              <span id="text">Sub Total </span>
              <span>
                <i class="fa-solid fa-indian-rupee-sign"></i> {subtotal} /-
              </span>
            </div>
            <div id="subtotal">
              <span id="text">Taxes and Charges </span>
              <span>
                <i class="fa-solid fa-indian-rupee-sign"></i>{" "}
                {Math.round(subtotal * 0.1).toFixed(2)} /-
              </span>
            </div>
            <div id="subtotal">
              <span id="text">Grand Total </span>
              <span>
                <i class="fa-solid fa-indian-rupee-sign"></i>{" "}
                {Math.round(subtotal * 0.1 + subtotal).toFixed(2)} /-
              </span>
            </div>
          </div>
          <div id="placeOrder">
            <Checkout
              subtotal={Math.round(subtotal * 0.1 + subtotal).toFixed(2)}
              currentUser={currentUser}
            />
          </div>
        </div>
      ) : (
        ""
      )}
    </div>
  );
}

export default Cart;
