import React from "react";
import "../css/Navbar.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { Dropdown } from "react-bootstrap";
import { useSelector, useDispatch } from "react-redux";
import { logoutUser } from "../Actions/userAction";

function Navbar() {
  const cartState = useSelector((state) => state.cartReducer);
  const userState = useSelector((state) => state.loginReducer);
  const { currentUser } = userState;
  const dispatch = useDispatch();

  function orderRoute() {
    window.location.href = "/orders";
  }

  return (
    <div className="navbar">
      <h1>
        <a href="/">
          {" "}
          <img
            src="https://png.pngtree.com/png-clipart/20221227/original/pngtree-delicious-pizza-slice-png-image_8811035.png"
            style={{ width: 40, height: 40 }}
          />
          Pizza Bar
        </a>
      </h1>
      <div className="right-div">
        <span className="loginLogout">
          {currentUser ? (
            <Dropdown className="dropdown">
              <Dropdown.Toggle
                variant="default"
                id="dropdown-basic"
                className="toggle"
              >
                {currentUser.name}
              </Dropdown.Toggle>

              <Dropdown.Menu className="dropdownMenu">
                <Dropdown.Item onClick={orderRoute}>My Orders</Dropdown.Item>
                <Dropdown.Item onClick={() => dispatch(logoutUser())}>
                  Logout <i class="fa-solid fa-arrow-right-from-bracket"></i>
                </Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          ) : (
            <a href="/login" className="Login">
              Login
            </a>
          )}
        </span>
        <span id="cartImage">
          <a href="/cart">
            <img
              src="https://cdn-icons-png.flaticon.com/512/1170/1170678.png"
              alt="cart-icon"
              style={{ height: 30, width: 30 }}
            />
            <div> {cartState.cartItems.length}</div>
          </a>
        </span>
      </div>
    </div>
  );
}

export default Navbar;
