import React from "react";
import "../css/register.css";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { registerAction } from "../Actions/userAction";
import { useToasts } from "react-toast-notifications";

function Register() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  const { addToast } = useToasts();

  const dispatch = useDispatch();
  const registerState = useSelector((state) => state.registerReducer);
  const { success, loading, error } = registerState;

  function register(e) {
    e.preventDefault();
    if (password != confirmPassword) {
      alert("password doest not matches");
    } else {
      const user = {
        name,
        email,
        password,
      };

      dispatch(registerAction(user));
    }
  }

  return (
    <div>
      <div className="row justify-content-center mt-5">
        <div className="col-md-3 mt-5">
          {success && (
            <div
              class="alert alert-success"
              role="alert"
              style={{ marginTop: -73 }}
            >
              User Registered Successfully
            </div>
          )}
          {error && (
            <div
              class="alert alert-warning"
              role="alert"
              style={{ marginTop: -73 }}
            >
              User Already Registered
            </div>
          )}
          <h2 className="text-center" style={{ color: "#83556e" }}>
            SIGN UP
          </h2>

          <div className="d-grid">
            <input
              type="text"
              placeholder="name"
              className="form-control"
              value={name}
              required
              onChange={(e) => setName(e.target.value)}
            />
            <input
              type="email"
              placeholder="email"
              className="form-control"
              value={email}
              required
              onChange={(e) => setEmail(e.target.value)}
            />
            <input
              type="password"
              placeholder="password"
              className="form-control"
              value={password}
              required
              onChange={(e) => setPassword(e.target.value)}
            />
            <input
              type="password"
              placeholder="confirm password"
              className="form-control"
              value={confirmPassword}
              required
              onChange={(e) => setConfirmPassword(e.target.value)}
            />
            <button className="btn btn-danger mt-3 " onClick={register}>
              {" "}
              SIGN UP
            </button>
          </div>
          <div style={{ marginTop: 10 }}>
            <span>Already have an account? </span>
            <a href="/login" style={{ textDecoration: "none" }}>
              <span
                className="text-center mt-4"
                style={{ fontSize: 20, color: "#9791ef" }}
              >
                Log In
              </span>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Register;
