import axios from "axios";

export const placeOrder = (token, subtotal) => {
  return async (dispatch, getState) => {
    dispatch({ type: "PLACE_ORDER_REQUEST" });
    const currentUser = getState().loginReducer.currentUser;
    const cartItems = getState().cartReducer.cartItems;

    try {
      const response = await axios.post("/api/orders/placeOrder", {
        token,
        subtotal,
        currentUser,
        cartItems,
      });
      dispatch({ type: "PLACE_ORDER_SUCCESS" });

      console.log(response, "18");
    } catch (error) {
      dispatch({ type: "PLACE_ORDER_FAILED" });
      console.log(error);
    }
  };
};

export const getOrders = () => {
  return async (dispatch, getState) => {
    dispatch({ type: "GET_ORDERS_REQUEST" });
    const currentUser = getState().loginReducer.currentUser;

    try {
      const response = await axios.post("/api/orders/getOrders", {
        userId: currentUser._id,
      });
      console.log(response, "35");
      dispatch({ type: "GET_ORDERS_SUCCESS", payload: response.data });

      console.log(response, "38");
    } catch (error) {
      dispatch({ type: "GET_ORDERS_FIALED", payload: error });
      console.log(error);
    }
  };
};
