import React from "react";
import StripeCheckout from "react-stripe-checkout";
import { useDispatch, useSelector } from "react-redux";
import { placeOrder } from "../Actions/orderAction";

function Checkout({ subtotal, currentUser }) {
  const dispatch = useDispatch();
  const orderState = useSelector((state) => state.placeOrderReducer);
  const { success, error } = orderState;

  function tokenHandler(token) {
    console.log("pressed place order button");
    dispatch(placeOrder(token, subtotal));
  }
  function cardDetails() {
    alert(
      "Demo card no : 5555 5555 5555 4444 " +
        "\n" +
        "Expiry date : any future date" +
        "\n" +
        "CVV : any three digit"
    );
  }
  return (
    <div>
      {success && (
        <div class="alert alert-success" role="alert" style={{ height: 70 }}>
          Your Order Placed Successfully
          <p>
            {" "}
            Check status in <b>my orders </b>
          </p>
        </div>
      )}
      {error && (
        <div class="alert alert-warning" role="alert">
          something went wrong
        </div>
      )}
      {currentUser ? (
        <StripeCheckout
          amount={subtotal * 100}
          shippingAddress
          token={tokenHandler}
          currency="INR"
          stripeKey="pk_test_51NaeGxSJm2yfzHbGxW5wne3NZTmcBiB0nIrvL1CkhW4EInekUYC3qqYc4muAaTTGjxrBMmIKQSLGxjKelSyh3GbD00loiTneLS"
        >
          <button onClick={cardDetails}>Place Order</button>
        </StripeCheckout>
      ) : (
        <div class="alert alert-warning" role="alert">
          <i class="fa-solid fa-circle-exclamation fa-beat-fade"></i> Please
          login to place orders
        </div>
      )}
    </div>
  );
}

export default Checkout;
