import { combineReducers, createStore, applyMiddleware } from "redux";

import thunk from "redux-thunk";
import { getAllPizzasReducer } from "../Reducers/pizzasReducer";

import { composeWithDevTools } from "redux-devtools-extension";
import { cartReducer } from "../Reducers/cartReducer";
import { loginReducer, registerReducer } from "../Reducers/userReducer";
import { placeOrderReducer, getOrdersReducer } from "../Reducers/orderReducer";

const finalReducer = combineReducers({
  getAllPizzasReducer: getAllPizzasReducer,
  cartReducer: cartReducer,
  registerReducer: registerReducer,
  loginReducer: loginReducer,
  placeOrderReducer: placeOrderReducer,
  getOrdersReducer: getOrdersReducer,
});

const cartItems = localStorage.getItem("cartItems")
  ? JSON.parse(localStorage.getItem("cartItems"))
  : [];

const currentUser = localStorage.getItem("currentUser")
  ? JSON.parse(localStorage.getItem("currentUser"))
  : null;

const initialState = {
  cartReducer: {
    cartItems,
  },
  loginReducer: {
    currentUser,
  },
};

const composeEnhencers = composeWithDevTools({});

const store = createStore(
  finalReducer,
  initialState,
  composeEnhencers(applyMiddleware(thunk))
);

export default store;
